(function() {
    /*globals  TweenMax:false */
    'use strict';

    angular
        .module('podiatry')
        .directive('videoPlayer', videoPlayer)
        .directive('videoPlayerControls', videoPlayerControls);

    /** @ngInject */
    function videoPlayerControls() {
        return {
            restrict: 'E',
            transclude: true,
            template: '<div ng-transclude></div>',
            link: function(scope,element){
                var controls = element.find('vg-controls')[0];
                var innerScope = angular.element(controls).isolateScope();

                var onplayHide = scope.$on('controls-hide', function(){
                    innerScope.hideControls();
                });

                scope.$destroy(function(){
                    onplayHide();
                })

            }
        }
    }

    /** @ngInject */
    function videoPlayer($log,$sce,$timeout,deviceDetector) {
        return {
            restrict: 'E',
            replace:true,
            templateUrl: 'app/components/videoheader/videoplayer.html',
            scope: {
                videoId: '=',
                status: '='
            },
            controller: function($scope){

                var vm = this;
                vm.ready = false;
                vm.controls;
                $log.debug('IS MOBILE',deviceDetector.isPhoneOnly())
                if(deviceDetector.isPhoneOnly()){
                  $timeout(function(){
                    vm.other = false;
                    vm.mobile = true;
                    vm.scalemode = 'contain'
                    var video = document.getElementsByTagName('video')[0];
                      var  onPlayerExitFullscreen = function(){

                      }
                      video.addEventListener('webkitendfullscreen', onPlayerExitFullscreen, false);
                    },0);
                }
                else{
                    vm.other = true;
                    vm.mobile = false;
                    vm.scalemode = 'cover'
                }

                vm.onPlayerReady = function($API){
                    $log.debug('PLAYER READY --------------------------------',$API)
                    $scope.API = $API;
                    $scope.$emit('videoPlayerInit',$scope.API);

                }

                vm.onCompleteVideo = function(){


                }

                vm.onStateChange = function($state){

                  $log.debug('PLAYER STATE',$state)

                    if($state === 'play'){
                      $scope.status = true;
                      $timeout(function(){
                        $scope.$emit('videoPlayerPlaying');
                        vm.controls.hideControls();
                      },100)

                    }
                }


              function initVideo(videoID) {
                var videoData = videoID;

                return {
                  autoPlay: false,
                  ready: true,
                  sources: [
                    {src: $sce.trustAsResourceUrl(videoData[0]), type: "video/mp4"},
                    {src: $sce.trustAsResourceUrl(videoData[2]), type: "video/webm"}

                  ],
                  transclude: true,
                  plugins: {
                    controls: {
                      autoHide: true,
                      autoHideTime: 10000
                    }
                  }
                }
              }

              vm.start = function(){
                $log.debug('video player start',$scope.videoId)
                $scope.config = initVideo($scope.videoId);
              }




            },
            controllerAs: 'main',
            link:function ($scope,$element,$attri, controller){


              if(!deviceDetector.isPhoneOnly() && !deviceDetector.isTablet()){
                $timeout(function(){
                  controller.controls = angular.element($element.find('vg-controls')[0]).isolateScope();
                  try{
                    controller.controls.hideControls();
                  }
                  catch(e){
                    $log.debug('No controls')
                  }
                },10)
              }

              controller.start();


            }


        };

    }
})();

/*
 * vg-bkg-size
 * A Videogular plugin to emulate background-size CSS property for video: "cover" or "contain"
 *
 * Use:
 * <videogular vg-bkg-size="cover" center="true"></videogular>
 * vg-bkg-size => "cover" or "contain"
 * center => true or false
 *
 * Copyright (c) 2014 Panurge Web Studio
 * Licensed under the MIT license.
 */
( function( angular ) {

    'use strict';
    angular
        .module('podiatry')
        .directive( 'vgBkgSize', [ '$window','$log',
        function( $window ) {

            return {
                restrict: 'A',
                require: '^videogular',

                link: function( $scope, elem, attrs, API ) {


                    var videoHeight, videoWidth, inited = false,
                        mode = attrs.vgBkgSize === 'contain' ? 'contain' : 'cover',
                        center = attrs.center === false ? false : true;

                    // API extension
                    API.vgBkgSize = API.vgBkgSize || {};

                    API.vgBkgSize.update = function() {


                        if ( !inited ) {
                            return;
                        }

                        //var video = angular.element(API.mediaElement[ 0 ]);
                        // get native video size
                        videoHeight = 720;//video.height();
                        videoWidth = 1280;//video.width();

                        API.vgBkgSize.layout();

                    };

                    API.vgBkgSize.layout = function() {



                        if ( !inited ) {
                            return;
                        }

                        // get wrapper size
                        var containerH = angular.element(elem[ 0 ]).height(),
                            containerW = angular.element(elem[ 0 ]).width();



                        var newW, newH;

                        var ratio = videoWidth / videoHeight;

                        if ( mode === 'contain' ) {
                            if ( containerH / containerW > videoHeight / videoWidth ) {
                                newW = containerW;
                                newH = containerW / ratio;

                            } else {
                                newW = containerH * ratio;
                                newH = containerH;

                            }
                        } else if ( mode === 'cover' ) {

                            newW = containerH * ratio;
                            newH = containerW / ratio;


                            if (newH < containerH){
                                newW = containerH * ratio;
                                newH = containerH;
                            }

                            if (newW < containerW) {
                                newH = containerW / ratio;
                                newW = containerW;
                            }



                        }


                        angular.element(API.mediaElement[ 0 ]).css( {
                            'width': newW + 2,
                            'height': newH + 2, // +2 pixels to safely prevent empty spaces
                            'max-height': 'none',
                            'max-width': 'none'
                        } );


                        if ( center ) {
                            angular.element(API.mediaElement[ 0 ]).css( {
                                'margin-top': ( containerH - ( newH + 2 ) ) / 2,
                                'margin-left': ( containerW - ( newW + 2 ) ) / 2
                            } );
                        } else {
                            angular.element(API.mediaElement[ 0 ]).css( {
                                'margin-top': '',
                                'margin-left': ''
                            } );
                        }

                    }

                    attrs.$observe( 'vgBkgSize', function( nv, ov ) {
                        if ( nv !== ov ) {
                            mode = nv === 'contain' ? 'contain' : 'cover';
                            API.vgBkgSize.layout();
                        }
                    } );

                    attrs.$observe( 'center', function( nv, ov ) {
                        if ( nv !== ov ) {
                            center = $scope.$eval( nv ) === false ? false : true;
                            API.vgBkgSize.layout();
                        }
                    } );

                    $scope.$watch(
                        function() {
                            return API.isReady;
                        },
                        function( newVal ) {

                                if ( newVal === true ) {
                                    inited = true;
                                    API.vgBkgSize.update();
                                }
                                else{
                                    inited = false;
                                }

                        }
                    );

                    $scope.$on("$vgBkgSizeUpdate", API.vgBkgSize.update);
                    $scope.$on("$vgBkgSizeLayout", API.vgBkgSize.layout);

                    angular.element( $window ).on( 'resize', API.vgBkgSize.layout );
                }
            };
        }
    ] );

} )( window.angular );

(function() {
  /*globals $:false TimelineMax:false TweenMax:false Strong:false*/
  'use strict';

  angular
    .module('podiatry')
    .directive('videoHeader', videoHeader);

  /** @ngInject */
  function videoHeader($log,$timeout,$state,$rootScope,deviceDetector) {
    return {
      restrict: 'E',
      replace:true,
      transclude:true,
      templateUrl: 'app/components/videoheader/videoHeader.html',
      scope: {
        content: '=',
        scroll: '='
      },
      controller: function($scope){
        var vm = this;
        vm.showVideo = false;
        vm.title = $scope.content.header;
        vm.text = $scope.content.body;
        vm.video = $scope.content.video;
        vm.playerStatus = null;
        vm.showVideo =false;
        vm.scroll = (typeof $scope.scroll !== 'undefined') ? $scope.scroll : true;
        vm.device = deviceDetector.isPhoneOnly();

        $log.debug('SCROLL SETTING',vm.scroll,$scope.scroll)

      },
      controllerAs: 'main',
      link: function($scope,$element,$attr,controller){

        var button = $element.find('#video-header-playbtn');
        var popup;
        var videoLocalChange;
        var videoinit;


        var ratio = function(){
          var r = 0;
          if($(window).width()<$(window).height()){
            r = ($(window).height()/100)/(500/100);
          }
          else{
            r = ($(window).width()/100)/(500/100);
          }

          return r
        };

        var showVid = function(){
          $timeout(function(){
            controller.showVideo = true;
          })
        }

        $(window).on('resize',function(){
          setOverlayCenter();
          popup = animation();
        });

        var setOverlayCenter = function(){
          var x = 0;
          var y = 0;
          if(!controller.device){
            y =  ($(button).offset().top -($('.video-in-circle').height()/2))+($('#video-header-playbtn').height()/2);
            x =  ($(button).offset().left-($('.video-in-circle').width()/2))+($('#video-header-playbtn').width()/2);
          }
          else{
            y =  ($(window).height()/2 -($('.video-in-circle').height()/2));
            x =  ($(window).width()/2-($('.video-in-circle').width()/2));
          }

          TweenMax.set('.video-in-circle',{x:x+20,y:y+20});
        }


        var animation  = function(){
          var tl = new TimelineMax({paused:true,onUpdate:function(){$log.debug('header playing')}});
          tl.set('.video-overlay',{autoAlpha:0});
          tl.set('.video-overlay',{scale:1});
          tl.fromTo('.video-in-circle',1,{scale:0,autoAlpha:1},{scale:ratio(),onComplete:showVid,ease:Strong.easeInOut},'intro_1');
          tl.to('#home h1',1,{y:-100,scale:1.2,opacity:0,ease:Strong.easeInOut},'=-1');
          tl.to('#home .body',1,{y:-100,scale:1.2,opacity:0,ease:Strong.easeInOut},'=-1');
          tl.to('#home .link',1,{y:100,scale:1.2,opacity:0,ease:Strong.easeInOut},'=-1');
          tl.addPause();
          tl.set('.video-overlay',{autoAlpha:1},'intro_2')
          tl.set('.video-in-circle',{autoAlpha:0});
          tl.to('.video-reveal',1,{xPercent:100,z:0,ease:Strong.easeOut});
          tl.addPause();
          tl.to('.video-reveal',1,{xPercent:0,z:0,ease:Strong.easeOut,onComplete:function(){controller.playerStatus = false;controller.showVideo = false;}},'outro_1');
          tl.set('#video-header-playbtn',{autoAlpha:0});
          tl.to('.video-overlay',1,{autoAlpha:0,scale:0.8,delay:0.2,ease:Strong.easeOut,onComplete:function(){controller.showVideoPlayer = false;$scope.$apply();}},'outro_2');
          tl.to('#home h1',1,{y:0,scale:1,opacity:1,ease:Strong.easeInOut},'=-1.2');
          tl.to('#home .body',1,{y:0,scale:1,opacity:1,ease:Strong.easeInOut},'=-1.2');
          tl.to('#home .link',1,{y:0,scale:1,opacity:1,ease:Strong.easeInOut},'=-1.2');
          tl.to('#video-header-playbtn',1,{autoAlpha:1},'=-0.5');
          tl.addPause();
          return tl;
        }


        $timeout(function(){
          popup = animation();
          controller.onShowVideo = function(){
            $rootScope.$broadcast('hideNav');

            videoLocalChange =  $scope.$on('$locationChangeSuccess',function(){
              if(controller.showVideo){
                $('body').removeClass('no-scroll');
                popup.play('outro_2',false);
                videoinit();
                videoLocalChange();
              }
            });

            videoinit = $scope.$on('videoPlayerInit',function(event,data){
              $log.debug('deviceDetector.isPhoneOnly()',deviceDetector.isTablet())
              if(!deviceDetector.isPhoneOnly() && !deviceDetector.isTablet()){
                $timeout(function(){
                  data.play();
                },100)
              }
              else{
                $timeout(function() {
                  data.pause();
                },100)
              }
              popup.play('intro_2',false);
            })

            setOverlayCenter();
            $state.go($state.current.name,{'#':'video'});

            TweenMax.to($(window),1,{scrollTo:{y:0}});
            controller.showVideoPlayer = true;

            if(!controller.scroll){
              $('body').addClass('no-scroll');
            }
            popup.play('intro_1',false);

          }
          controller.cancel = function(){
            videoinit();
            videoLocalChange();
            $('body').removeClass('no-scroll');
            $state.go($state.current.name,{'#':''});
            popup.play('outro_1',false);
            $rootScope.$broadcast('showNav');
          }

          $scope.$watch(function(){return controller.playerStatus}, function(n,o){
            if(n===null){
              return;
            }
            if(n!==o){
              return;
            }
            if(!n){
              popup.play('outro_2',false);
            }
          })
          },100)

          $scope.$on('$destroy', function () {
            try{
              videoinit();
              videoLocalChange();
            }
            catch(e){
              $log.debug('NO EVENT')
            }

          })
      }
    };

  }

})();

