(function() {
  /*globals $:false  TweenMax:false Power4:false*/
  'use strict';

  angular
    .module('podiatry')
    .directive('asyncAnchor', asyncAnchor)

  /** @ngInject */
  function asyncAnchor($log,$rootScope,$location,$state, $anchorScroll,$timeout) {

    return {
      restrict:'A',

      link: function(scope, element, attributes){

        var id = attributes.asyncAnchor || attributes.id || attributes.name;
        var scrollContainer = $(window);
        var spyScrollAnim;
        var spyScroll;
        var currentState;
        var delay = attributes.asyncAnchorDelay;
        var eventName = attributes.asyncAnchorEvent;
        var firstTimeScrolling = true;
        var spy = attributes.asyncAnchorSpy = true;
        var scrolling = false;
        var scrollAnim = false;

        $.fn.scrollEnd = function(callback, timeout) {
          $log.debug('callback', callback,timeout)
          $(this).scroll(function(){
            var $this = $(this);
            if ($this.data('scrollTimeout')) {
              clearTimeout($this.data('scrollTimeout'));
            }
            $this.data('scrollTimeout', setTimeout(callback,timeout));
          });
        };

        var scrollSpy = function(){
          scrolling = true;
          $(element).find('article').each(function(){
            if(($(this).offset().top - 85) <  scrollContainer.scrollTop() && $(this).offset().top >  scrollContainer.scrollTop()){
              if(spy){

                if(typeof $(this).attr('id') === 'undefined'){
                  return;
                }

                if($(this).attr('id')!==currentState){
                  currentState = $(this).attr('id');
                  $state.go('app.home',{'#':$(this).attr('id')});
                }

              }
            }
          })
        }

        var scrollToHash = function(hash){
          if(id && hash && id !== hash){
            if(delay && firstTimeScrolling){
              $timeout(function(){
                if(typeof $('#'+hash).offset() !== 'undefined')
                  scrollContainer.scrollTop( $('#'+hash).offset().top -80 );
              }, delay);
            }else{
              if(typeof $('#'+hash).offset() !== 'undefined')
                if(!scrollAnim){
                  scrollContainer.scrollTop( ($('#'+hash).offset().top)-80 );
                }
                else{
                  TweenMax.to(scrollContainer, 2, {
                    scrollTo: {y: (($('#'+hash).offset().top)-80), autoKill: false},
                    onComplete: function () {
                      scrollAnim = false;
                    },
                    ease: Power4.easeOut
                  });
                }

            }
            firstTimeScrolling = false;
          }
        };


        function init(){
          if (spy) {
            $timeout(function () {
              spyScroll = scope.$on('spy-scroll', function (event, message) {
                if (message !== 'start') {
                  spy = true;
                }
                else {
                  spy = false;
                }
              });

              spyScrollAnim = scope.$on('scroll-anim', function (event, obj) {
                scrollAnim = obj.scrolling;
                if(typeof obj.link !== 'undefined'){
                  scrollToHash(obj.link);
                }

              });

              scrollContainer.on('scroll', scrollSpy);

              scrollContainer.scrollEnd(function () {
                scrolling = false;
              }, 1000);


            }, 1000);
          }
        }

        if(eventName){
          scope.$on(eventName, function(){
            scrollToHash($location.hash());
          });
        }

        var scrollToTop =   $rootScope.$on('scrollToTop', function(e, func){
          scrollAnim = true;
          if(scrollContainer.scrollTop() !== 0) {
            TweenMax.to(scrollContainer, 0.8, {
              scrollTo: {y: 0, autoKill: false},
              onComplete: function () {
                scrollAnim = false;
                func();
              },
              ease: Power4.easeInOut
            });
          }
          else{
            func();
          }


        });

        scope.$on('$destroy', function(){
          scrollContainer.off('scroll', scrollSpy);
          scrollToTop();
          spyScroll();
          spyScrollAnim();

        });

        scope.$watch(function(){
          return $location.hash();
        }, function(hash){
          if(!scrolling){
            $timeout(function() {
              scrollToHash(hash,spy);
            },0);
          }

        });

        init();

      }
    };

  }
})();

