(function() {
  'use strict';

  angular
    .module('podiatry')
    .directive('videoButton', videoButton);

  /** @ngInject */
  function videoButton($modal,$timeout) {
    return {
      restrict: 'E',
      replace:true,
      templateUrl: 'app/components/pagecomps/videobutton.html',
      scope: {
        video: '=',
        label: '='
      },
      controller: function($scope){

        var vm = this;
        vm.video = $scope.video;
        vm.label = $scope.label;

        vm.openVideo = function(url){
          var modalInstance = $modal.open({
            size:'large',
            templateUrl:'app/components/pagecomps/videoModal.html',
            controller: function($scope,$modalInstance,items, $sce){
              $scope.showClose = true;
              $scope.config = {

                api: null,
                preload: "none",
                autoPlay:true,
                sources: [
                  {src: $sce.trustAsResourceUrl(''+url+'&wmode=transparent'), type: "video/mp4"}

                ],
                tracks: [
                  {
                    src: "http://www.videogular.com/assets/subs/pale-blue-dot.vtt",
                    kind: "subtitles",
                    srclang: "en",
                    label: "English",
                    default: ""
                  }
                ],
                plugins: {
                  controls: {
                    autoHide: true,
                    autoHideTime: 5000
                  }
                }
              }

              $timeout(function(){
                $scope.showClose = false;
              },1000)

              $scope.cancel = function () {
                $scope.showClose = true;
                $modalInstance.dismiss('cancel');
              };
            },
            resolve: {
              items: function(){
                return url;
              }

            }
          });
        }

      },
      controllerAs: 'main'
    };
  }

})();

