(function($) {
  /*globals jQuery TweenMax Draggable:false*/
  'use strict';

  angular
    .module('podiatry')
    .directive('scrolltopanimate', scrolltopanimate);

  /** @ngInject */
  function scrolltopanimate($timeout) {
    return {
      restrict: 'A',
      scope: {
        elem: '='
      },
      controller: function () {

      },
      controllerAs:'scrolltop',
      link: function ($scope) {

        $scope.onScroll = function(){
          var scrollfactor = Math.min(1, $(window).scrollTop() / $('#master-header').height());
          if($scope.active){
            if(scrollfactor==1){
              TweenMax.to('.footer-btn-top',0.5,{autoAlpha:1});
            }
            else{
              TweenMax.to('.footer-btn-top',0.5,{autoAlpha:0});
            }
          }
        };

        $timeout(function(){

          $(window).on('scroll',$scope.onScroll);

          $scope.$watch(function(){return $scope.active},function(n){
            $scope.show = n;
          })
          $scope.active = true;
        },500);

      }
    }
  }
})(jQuery);

