(function() {
  /*globals $:false TweenMax:false Power2:false*/
  'use strict';

  angular
    .module('podiatry')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    return {
      restrict: 'E',
      replace:true,
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
        index: '='
      },

      controller:  function ($log,$scope,$rootScope, $timeout, contentService,$state,$previousState,$location,deviceDetector) {
        var vm = this;
        var open = false;
        vm.activeMenu = true;


        var scrollCheck = function(){
          var s = false;
          var factor = Math.min(1, $(window).scrollTop() / $('#master-header').height());
          if (factor != 1) {
            s = false;
          }
          else {
            s = true;
          }
          vm.navShow = s;
          $scope.$apply();
        }

        vm.goto = function(hash){
          vm.autoClose();
          TweenMax.to($(window), 2, {scrollTo:{y:(($('#'+hash).offset().top)-100)}, ease:Power2.easeOut});
          $scope.index = hash;
        }

        var checkRoute = function(state){
          $log.debug('checkroute',state)
          $(window).off('scroll',scrollCheck);
          $timeout(function(){

            switch(state){
              case 'app.home.videowall':{
                $log.debug('videowall')
                vm.navShow = false;
                vm.activeMenu = true;
                break;
              }
              case 'app.professional':{
                vm.navShow = true;
                vm.activeMenu = false;
                break;
              }
              case 'app.socialwall':{
                vm.navShow = true;
                vm.activeMenu = false;
                break;
              }
              case 'app.home.mobile':{
                vm.navShow = false;
                vm.activeMenu = true;
                break;
              }
              case 'app.home':{

                if(deviceDetector.isPhoneOnly()){
                  $log.debug('home - mobile')
                  vm.navShow = true;
                  vm.activeMenu = true;
                  return;
                }
                else{
                  $log.debug('home')
                  vm.navShow = false;
                  $timeout(function(){
                    vm.activeMenu = true;
                    scrollCheck();
                    $(window).on('scroll',scrollCheck);
                  },1000);
                }
                break
              }
            }
          },0);

        };

        vm.toggle = function(){
          if(open){
            $('.is-dropdown-submenu-parent').removeClass('is-active');
            $('.is-dropdown-submenu').removeClass('js-dropdown-active');
            open = false;
          }
          else{
            open = true;
          }
        }

        vm.autoClose = function(){
          $timeout(function(){
            $('.is-dropdown-submenu-parent').removeClass('is-active');
            $('.is-dropdown-submenu').removeClass('js-dropdown-active');
          },1000)
        };

        vm.cancel = function(){

          vm.activeMenu = true;

          if($state.current.name === 'app.home.videowall'){
            $state.go('app.home',{'#':'first_step'});
            return;
          }

          if($state.current.name === 'app.home.socialwall'){
            $state.go('app.home',{'#':'social'});
            $('#social').scrollTop(0);
            return;
          }

          if($state.current.name === 'app.professional'){
            if($location.hash()==='video'){
              $state.go('app.professional',{'#':''});
              return;
            }
          }

          if($previousState.get()!==null){
            $previousState.go();
          }
          else{
            $state.go('app.home');
          }
        }

        $timeout(function(){
          checkRoute($state.current.name);
        },0);



        $scope.$watch(function(){return contentService.content},function(n,o){
          vm.content = contentService.content;
        })

        $scope.$on('$stateChangeSuccess',function(event, toState,toParam){
          $log.debug('$stateChangeSuccess',$location.hash(),toParam)
          checkRoute(toState.name);
        });

        $rootScope.$on('showNav',function(){
          checkRoute($state.current.name);
        });

        $rootScope.$on('hideNav',function(){
          vm.navShow = false;
        });



      },
      controllerAs: 'main',
      bindToController: true,
      link: function (scope, elements, attrs) {
        if(attrs.ngClick || attrs.href === '' || attrs.href === '#'){
          elements.on('click', function(e){
            e.preventDefault();
          });
        }
      }
    };

  }

})();

(function() {
  'use strict';

  angular
    .module('podiatry')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

