(function($) {
  /* globals jQuery TweenMax Back:false, TimelineMax:false */
  'use strict';

  angular
    .module('podiatry')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($log,$sce,$timeout,contentService,ScrollMagicService,deviceDetector) {
    var vm = this;
    vm.content = contentService.content;
    vm.mobile =  deviceDetector.isPhoneOnly();


    $timeout(function () {
      if(!deviceDetector.isPhoneOnly()) {
        $log.debug('ANIMATION');
      var socialScene = ScrollMagicService.getScene('socialAnim');
      var scene_pointer = ScrollMagicService.getScene('pointer');
      var videoBlockAnim_1 = ScrollMagicService.getScene('videoBlockAnim_1');
      var videoBlockAnim_2 = ScrollMagicService.getScene('videoBlockAnim_2');
      var courseAnim = ScrollMagicService.getScene('courseAnim');
      var timeAnim = ScrollMagicService.getScene('timeAnim');
      var uniAnim = ScrollMagicService.getScene('uniAnim');

      //social
      var social = new TimelineMax();
      social.from('.social-container', 0.5, {backgroundPositionX: '-100%'});
      social.staggerFrom('.social-hashtags > li', 0.5, {
        opacity: 0,
        scale: 0.5,
        y: '20%',
        ease: Back.easeIn
      }, 0.1, '=-0.5');
      social.staggerFrom('.social-links > li', 0.5, {
        opacity: 0,
        scale: 0.5,
        y: '20%',
        ease: Back.easeIn
      }, 0.1, '=-0.5');
      socialScene.reverse(false)
      socialScene.setTween(social);

      //pointer
      var pointer = new TimelineMax();
      pointer.from('#pointerLineAnim', 1, {height: 0});
      pointer.from('#pointerDotAnim', 0.5, {scale: 0});
      pointer.from('#pointerDotLargeAnim', 0.3, {scale: 0}, '=-0.2');

      videoBlockAnim_1.reverse(false);
      videoBlockAnim_2.reverse(false);
      timeAnim.reverse(false);
      courseAnim.reverse(false);
      uniAnim.reverse(false);


      scene_pointer.setTween(pointer);
      }

    }, 100);


    vm.getContent = function($id){
      return contentService.getContentById($id);
    }

    vm.getUniversities = function (){
      var data = contentService.getContentById(4).map.universities;
      return data;
    }

    vm.trusted = function (url){
      return $sce.trustAsResourceUrl(url);
    }

    vm.onScrolltop = function(){
      TweenMax.to($(window),1,{scrollTo:{y:0}});
    }



  }
})(jQuery);

