(function() {
  'use strict';

  angular
    .module('podiatry')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');


  }

})();

(function() {
  'use strict';

  angular
    .module('podiatry')
    .config(routerConfig);


  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('app', {
        abstract:true,
        resolve:{
          contentData: function (contentService) {
            return contentService.fetchContent();
          }
        },
        controller:function($scope,contentData,contentService,preloader,$log){
          
          $scope.isLoading = true;
          $scope.isSuccessful = false;
          $scope.percentLoaded = 0;

          var imageLocations = [];

          imageLocations.push('/assets/images/header.jpg');
          imageLocations.push('/assets/images/first-steps-christie.jpg');
          imageLocations.push('/assets/images/first-steps-faye.jpg');
          imageLocations.push('/assets/images/first-steps-hinson.jpg');
          imageLocations.push('/assets/images/first-steps-megan.jpg');
          imageLocations.push('/assets/images/first-steps-usamah.jpg');
          imageLocations.push('/assets/images/social-wall-bg.jpg');
          imageLocations.push('/assets/images/uni-map.jpg');

          // Preload the images; then, update display when returned.
          preloader.preloadImages( imageLocations ).then(
            function handleResolve() {
              // Loading was successful.
              $scope.isLoading = false;
              $scope.isSuccessful = true;
              $log.info( "Preload Successful" );
            },
            function handleReject( imageLocation ) {
              // Loading failed on at least one image.
              $scope.isLoading = false;
              $scope.isSuccessful = false;
              $log.error( "Image Failed", imageLocation );
              $log.info( "Preload Failure" );
            },
            function handleNotify( event ) {
              $scope.percentLoaded = event.percent;
              $log.info( "Percent loaded:", event.percent );
            }
          );

        },
        controllerAs:'main'
      })
      .state('app.home', {
        url: '/',
        views:{
          'content':{
            templateUrl: 'app/main/main.html',
            controller: 'MainController',
            controllerAs: 'main'
          }
        },
        data: {
          'gsapifyRouter.content': {
            enter: {
              in: {
                transition: {
                  duration: 1,
                  ease: 'Quart.easeInOut',
                  css: {
                    x: '-100%'
                  }
                },
                priority: 1
              },
              out: {
                transition: {
                  duration: 1,
                  ease: 'Quart.easeInOut',
                  css: {
                    x: '100%'
                  }
                },
                priority: 1
              }
            },
            leave: {
              in: {
                transition: {
                  duration: 1,
                  ease: 'Quart.easeInOut',
                  css: {
                    x: '100%'
                  }
                },
                priority: 1
              },
              out: {
                transition: {
                  duration: 1,
                  ease: 'Quart.easeInOut',
                  css: {
                    x: '-100%'
                  },
                  priority: 1
                }
              }
            }
          }
        }
      })

      .state('app.home.videowall', {
        url: 'video/:video',
        views: {
          'videowallplayer':{
            templateUrl: 'app/components/pagecomps/videowallplayer.html',
            controller: 'VideowallController',
            controllerAs: 'main'
          }
        },
        data: {
          'gsapifyRouter.videowallplayer': {
            enter: {
              in: {
                transition: {
                  duration: 1,
                  ease: 'Quart.easeInOut',
                  css: {
                    opacity: 1
                  }
                },
                priority: 1
              },
              out: {
                transition: {
                  duration: 1,
                  ease: 'Quart.easeInOut',
                  css: {
                    opacity: 0
                  }
                },
                priority: 1
              }
            },
            leave: {
              in: {
                transition: {
                  duration: 1,
                  ease: 'Quart.easeInOut',
                  css: {
                    opacity: 1
                  }
                },
                priority: 1
              },
              out: {
                transition: {
                  duration: 1,
                  ease: 'Quart.easeInOut',
                  css: {
                    opacity: 0
                  },
                  priority: 1
                }
              }
            }
          }
        }
      })

      .state('app.professional', {
        url: '/professional-body',
        views:{
          'content':{
            templateUrl: 'app/professional/professional.html',
            controller: 'ProfessionalController',
            controllerAs: 'main'
          }
        }
      })

      .state('app.socialwall', {
        url: '/socialwall',
        views:{
          'content':{
            templateUrl: 'app/socialwall/socialwall.html',
            controller: 'SocialwallController',
            controllerAs: 'main'
          }
        }
      })

      .state('app.home.mobile', {
        url: 'mobile/:content/:id',
        views:{
          'videoplayermobile':{
            templateUrl: 'app/components/pagecomps/videoplayermobile.html',
            controller: 'VideoMobileController',
            controllerAs: 'main'
          }
        },
        data:{
          'gsapifyRouter.videoplayermobile': {
            enter: {
              in: {
                transition: {
                  duration: 1,
                  ease: 'Quart.easeInOut',
                  css: {
                    opacity:0
                  }
                }
              },
              out: {},
              priority: 1
            },
            leave:{
              in:{},
              out:{
                transition: {
                  duration: 1,
                  ease: 'Quart.easeInOut',
                  css: {
                    opacity:0
                  }
                }
              }
            }

          }

        }
      })
    ;

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('podiatry')
    .constant('malarkey', malarkey)
    .constant('moment', moment)
    .constant('contentUrl','/assets/content/');

})();

(function() {
  'use strict';

  angular
    .module('podiatry')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig,scrollMagicProvider,$anchorScrollProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;

    scrollMagicProvider.addIndicators = false;
    $anchorScrollProvider.disableAutoScrolling();
  }

})();

